.itemsContainer {
  display: flex;
  flex-wrap: wrap;
}
.cardContainer {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  align-content: stretch;
  width: calc((100% - 6 * 10px) / 3);
  height: 500px;
  border-radius: 2px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  margin: 5px;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.cardContainer:focus,
.cardContainer:hover {
  transform: scale(1.03);
  cursor: zoom-in;
  box-shadow: 0px 0px 2px 2px rgba(14, 13, 13, 0.2),
    0px 0px 2px 2px rgba(0, 0, 0, 0.14),
    0px 0px 2px 2px rgba(219, 14, 116, 0.12);
}

.listOfCard {
  width: 1000px;
  margin-left: auto;
  margin-right: auto;
}
.catalogInformation {
  margin-bottom: 10px;
  font-size: 24px;
}
.calculetedData {
  font-weight: 500;
  color: #0000ff;
}
.deleteButton {
  width: 100%;
  margin: 10px 0px;
  padding: 5px;
  background-color: #00008b;
  border: none;
  border-radius: 5px;
  color: aliceblue;
  cursor: pointer;
}
.deleteButton:hover,
.deleteButton:focus {
  background-color: #dc143c;
}
.itemContent {
  flex-grow: 1;
}
.noItem {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
